<template>
  <section class="page-layout" :class="{'fullScreen': isfullScreen}" :style="{width: layout.width, height: layout.height}">
    <div ref="editorElem" v-loading="loading" style="height: 100%"></div>
    <el-button type="text" :title="isfullScreen ? '' : ''" class="fullScreenBtn" @click="setfullScreen">{{isfullScreen ? "" : ""}}</el-button>
  </section>
</template>

<script>
import E from "wangeditor";
import { mapState } from "vuex";
import { warningMsg, uuid, errorMsg } from "@/commonutils/common";
import appData from "@/assets/js/emoji.json";
import * as aliRequest from "@/commonutils/aliOSS";

export default {
  name: "index",
  props: {
    content: {
      type: String,
      default: ""
    },
    layout: {
      type: Object,
      default() {
        return {
          width: "100%",
          height: "100%"
        };
      }
    }
  },
  data() {
    return {
      FILE_TYPE_AVATAR: process.env.VUE_APP_FILE_TYPE_AVATAR,
      SUCC_CODE: process.env.VUE_APP_BASE_SUCC_CODE,
      ERR_TOKEN_CODE: process.env.VUE_APP_BASE_ERR_TOKEN_CODE,
      loading: false,
      faceList: [],
      editor: null,
      isfullScreen: false,
      isChange: false
    };
  },
  watch: {
    content(val) {
      if (!this.isChange) {
        this.editor.txt.html(val);
      }
      this.isChange = false;
    }
  },
  computed: {
    ...mapState(["user", "aliyunOss"])
  },
  created() {
    this.faceContent();
  },
  methods: {
    setfullScreen() {
      this.isfullScreen = !this.isfullScreen;
    },
    /**
     *
     * @return {void}
     */
    faceContent() {
      for (let i in appData) {
        this.faceList.push(appData[i].char);
      }
    }
  },
  mounted() {
    const this_ = this;
    this.editor = new E(this.$refs.editorElem);
    // html
    this.editor.customConfig.onchange = html => {
      this.isChange = true;
      this.$emit("update:content", html);
    };
    this.editor.customConfig.height = "100%";
    this.editor.customConfig.uploadImgMaxSize = 5 * 1024 * 1024; // 5M
    this.editor.customConfig.uploadImgMaxLength = 1; // 
    this.editor.customConfig.customAlert = function(info) {
      warningMsg(this_, info);
    };
    this.editor.customConfig.customUploadImg = async (files, insert) => {
      let imgFile = files[0];
      this_.loading = true;
      if (
        imgFile.type === "image/jpg" ||
        imgFile.type === "image/png" ||
        imgFile.type === "image/jpeg"
      ) {
        imgFile = await aliRequest.CompressorImg(files[0]);
      }
      const client = await aliRequest.OssRequset();
      const _obj = this.aliyunOss.imgPath + "/" + uuid();
      client
        .put(_obj, imgFile)
        .then(r1 => {
          insert(this.aliyunOss.imgHost + r1.name);
          this_.loading = false;
        })
        .catch(err => {
          errorMsg(this, err);
          this_.loading = false;
        });
    };
    this.editor.customConfig.menus = [
      "head", // 
      "bold", // 
      "fontSize", // 
      "fontName", // 
      "italic", // 
      "underline", // 
      "strikeThrough", // 
      "foreColor", // 
      "backColor", // 
      "link", // 
      "list", // 
      "justify", // 
      "quote", // 
      "emoticon", // 
      "image", // 
      // 'table', // 
      "code", // 
      "undo", // 
      "redo", // 
      "fullscreen"
    ];
    this.editor.customConfig.zIndex = 0;
    this.editor.customConfig.emotions = [
      {
        // tab 
        title: "",
        // type -> 'emoji' / 'image'
        type: "image",
        // content -> 
        content: [
          {
            alt: "[]",
            src:
              "http://img.t.sinajs.cn/t4/appstyle/expression/ext/normal/50/pcmoren_huaixiao_org.png"
          },
          {
            alt: "[]",
            src:
              "http://img.t.sinajs.cn/t4/appstyle/expression/ext/normal/40/pcmoren_tian_org.png"
          }
        ]
      },
      {
        // tab 
        title: "emoji",
        // type -> 'emoji' / 'image'
        type: "emoji",
        // content -> 
        content: this.faceList
      }
    ];
    this.editor.create(); // 
  }
};
</script>
<style lang="scss" scoped>
.page-layout {
  position: relative;
  &.fullScreen {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 9999;
    height: 100vh !important;
    width: 100% !important;
  }
  .fullScreenBtn {
    position: absolute;
    z-index: 1;
    top: 7px;
    right: 10px;
    padding: 0;
    span {
      font-size: 22px;
    }
  }
}
</style>
<style lang="scss">
.w-e-text-container{
  height: calc(100% - 50px) !important;/*!importantdiv300px*/
}
</style>
